module.exports = "### Dev Connect\n\nEl API de Dev Connect permite a las aplicaciones generar el Access Token requerido para acceder a los servicios de los Comercios administrados de manera rápida y simple para los comercios.\n\n#### API-Key\n\n```hint\nPara comunicarse con nuestro API es necesario poseer un API Key y un Access Token. Para obtenerlos se deben seguir los siguientes pasos\n\n1) Ingresar a [Consola](https://mobbex.com/console)\n2) Crear una cuenta si no posee\n3) Una vez creada la cuenta de usuario, crear una Entidad utilizando el CUIT de la empresa.\n4) Dirigirse desde el menú superior (3 puntos) a “Desarrollador”\n5) Crear una nueva aplicación o utilizar una existente.\n```\n\n#### URL del Servicio\n\n```code\nhttps://mobbex.com/p/developer/connect\n```\n\n#### Cabeceras (Headers)\n- ___x-api-key___: Clave API de su aplicación\n\n### Servicios\n\n#### Creación de Solicitud\n- ___Tipo___ POST\n- ___Url___ /\n\n__Parámetros__\n- ```return_url```: URL a la cual Mobbex deberá redireccionar al usuario al finalizar el proceso.\n- \n__Respuesta__\n\n```code\nlang: JSON\n---\n{\n    \"result\": true,\n    \"data\": {\n        \"id\": \"oZWpdzX7y\",\n        \"url\": \"https://mobbex.com/p/developer/connect/oZWpdzX7y\"\n    }\n}\n```\n\n*Importante* Redireccionar al usuario a la URL devuelta en data para la conexión. Si el resultado es positivo el usuario será redireccionado a la url declarada en \"return_url\" agregando el parametro \"connectStatus\" en \"done\". Si el resultado es negativo será reenviado a la misma url con el status \"cancelled\". Si el status es Done se debe llamar a la siguiente URL para solicitar las credenciales.\n\n\n#### Obtención de Credenciales\n- ___Tipo___ GET\n- ___Url___ /{Connect ID}/credentials\n\n__Parámetros__\n- ```Connect ID```: ID Retornado en la solicitud\n\n__Respuesta__\n\n```code\nlang: JSON\n---\n{\n    \"result\": true,\n    \"data\": {\n        \"connectId\": \"oZWpdzX7y\",\n        \"access_token\": \"XXXXXX-XXXXXXXXXX-XXXXXXXXXX-XXXXXXXXX\",\n        \"entity\": {\n            \"name\": \"Comercio de Prueba\",\n            \"logo\": {\n                \"filename\": \"comercio_de_prueba.png\",\n                \"url\": \"https://s3.amazonaws.com/mobbex/files/images/comercio_de_prueba.png\",\n                \"mimetype\": \"image/png\",\n                \"extension\": \"png\",\n                \"size\": 4472,\n                \"width\": 250,\n                \"height\": 250,\n                \"sizes\": [\n                    \"medium\",\n                    \"small\",\n                    \"square\"\n                ],\n                \"versions\": [\n                    \"https://s3.amazonaws.com/mobbex/files/images/comercio_de_prueba-medium.png\",\n                    \"https://s3.amazonaws.com/mobbex/files/images/comercio_de_prueba-small.png\",\n                    \"https://s3.amazonaws.com/mobbex/files/images/comercio_de_prueba-square.png\"\n                ],\n                \"created\": \"2017-12-01T10:20:17.000Z\"\n            },\n            \"tax_id\": \"27121231230\"\n        }\n    }\n}\n```\n"

